<template>
  <a-upload-dragger
    accept=".xlsx, .xlc, .xlm, .xls"
    :multiple="false"
    action=""
    :showUploadList="false"
    :beforeUpload="beforeUpload"
  >
    <p class="ant-upload-drag-icon">
      <a-icon type="inbox" />
    </p>
    <p class="ant-upload-text">单击或拖动文件到此区域上载</p>
    <p class="ant-upload-hint">
      支持单个文件上传。 只能上传xlsx文件，且不超过500kb
    </p>
  </a-upload-dragger>
</template>

<script>
/* eslint-disable */
import { file2Xce } from "@/utils/xlsxUtil";

export default {
  name: "Excel",
  data() {
    return {
      title: "批量添加",
      uploadColumn: {
        body: { field: "body", rule: this.$Rules.notNull("题干验证出错") },
        a: { field: "a" },
        b: { field: "b" },
        c: { field: "c" },
        d: { field: "d" },
        answer: { field: "answer" },
        note: { field: "note" },
      },
      tableDataUpload: [],
      percent: 0,
      error: false,
      errmsg: "数据验证出错",
      tableData: [
        {
          body: "题干内容",
          a: "A选项",
          b: "B选项",
          c: "C选项",
          d: "D选项",
          answer: "正确答案必须是数字（1:A,2:B,3:C,4:D）",
          note: "答案的解释（选填）",
        },
      ],
    };
  },
  methods: {
    // 导出模板的操作
    exportExcel() {
      require.ensure([], () => {
        const { export_json_to_excel } = require("@/excel/Export2Excel");
        const tHeader = ["body", "a", "b", "c", "d", "answer", "note"];
        // 上面设置Excel的表格第一行的标题
        const filterVal = ["body", "a", "b", "c", "d", "answer", "note"];
        // 上面的index、nickName、name是tableData里对象的属性
        const list = this.tableData; // 把data里的tableData存到list
        const data = this.formatJson(filterVal, list);
        export_json_to_excel(tHeader, data, "批量添加题库模板");
      });
    },
    formatJson(filterVal, jsonData) {
      return jsonData.map((v) => filterVal.map((j) => v[j]));
    },
    beforeUpload(file, fileList) {
      const fileSplit = file.name.split(".");
      const types = fileSplit[fileSplit.length - 1];
      const fileType = ["xlsx", "xlc", "xlm", "xls", "xlt"].some(
        (item) => item === types
      );
      file2Xce(file).then(async (tabJson) => {
        console.log("解析的数据", tabJson[0]);
        // 初始化
        this.tableDataUpload = [];
        this.percent = 0;
        this.error = false;
        try {
          if (tabJson && tabJson.length > 0) {
            if (tabJson[0].sheet) {
              // sheet1 数据
              const dataSheet1 = tabJson[0].sheet;
              const unit = 99 / dataSheet1.length;
              // 遍历 sheet1 数据
              for (let i = 0; i < dataSheet1.length; i++) {
                const data = {};
                for (const key of Object.keys(this.uploadColumn)) {
                  const item = this.uploadColumn[key];
                  const value = dataSheet1[i][item.field]; // 数据
                  if (item.rule) {
                    // 需要进行权限验证
                    const validator = await this.$Rules.rule(item.rule, value);
                    // 如果验证不通过则抛出异常
                    if (validator) {
                      throw "第" + (i + 1) + "行" + validator;
                    }
                  }
                  data[key] = value ? `${value}` : undefined;
                }
                this.tableDataUpload.push(data);
                this.percent = this.percent + unit;
              }
              this.percent = 100;
              // 结束
              console.log("tableDataUpload", this.tableDataUpload);
            }
          }
        } catch (error) {
          this.errmsg = error;
          this.error = true;
          this.$notification.error({ message: error });
        }
      });
      return false;
    },
    async onConfirm() {
      if (this.tableDataUpload.length > 0) {
        this.confirmLoading = true;
        try {
          const result = await this.$api.PostIssueList(this.tableDataUpload);
          if (result.code === 0) {
            this.$message.success("添加成功！");
            this.$emit("success");
            this.visible = false;
          } else {
            this.$message.error("添加失败！" + result.msg);
          }
        } catch (error) {
          this.$message.error("添加异常！" + error.message);
        }
        this.confirmLoading = false;
      }
    },
    show() {
      this.tableDataUpload = [];
      this.percent = 0;
      this.confirmLoading = false;
      this.error = false;
      this.visible = true;
    },
  },
};
</script>
